.header-section {
    background-color: #333; /* Header background color */
    color: white; /* Header text color */
    padding: 20px; /* Header padding */
    text-align: center; /* Center text */
  }
  
  .container {
    max-width: 600px; /* Max width of the container */
    margin: 0 auto; /* Center the container horizontally */
  }
  
  form {
    background-color: rgb(60, 157, 239); /* Form background color */
    border: 1px solid black; /* Form border */
    padding: 20px; /* Form padding */
    width: 100%; /* Form width */
    box-sizing: border-box; /* Include padding and border in the width */
  }
  
  form label {
    display: block; /* Display labels as block elements */
    margin-bottom: 5px; /* Add some space below the labels */
  }
  
  form input {
    width: 100%; /* Input width */
    padding: 5px; /* Input padding */
    margin-bottom: 10px; /* Add some space below the inputs */
    box-sizing: border-box; /* Include padding and border in the width */
  }
  
  .custom-register-button {
    background-color: #333; /* Button background color */
    color: white; /* Button text color */
    padding: 10px 20px; /* Button padding */
    border: none; /* Remove button border */
    cursor: pointer; /* Add cursor pointer on hover */
  }
  
  .link-btn {
    color: #333; /* Link text color */
    text-decoration: none; /* Remove underline from links */
    font-weight: bold; /* Make link text bold */
  }
  
  .link-btn:hover {
    text-decoration: underline; /* Underline link text on hover */
  }
  